.blog_container{
  background-image: linear-gradient(to bottom right, $yellow,#db36a4);
  border-radius: 10px;
  position: relative;
  top: 10vh;
  height: 87vh;
  // background-color: blue;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  
  .blog_main{
    display: flex;
    justify-content: space-evenly;
    // background-color: green;
    .blog_image{
      padding: 1rem;
      margin:1rem;
       width:250px;
       height: 250px;
       img{
         border-radius: 5px;
         width:100%;
         height: 100%;
       }
       @include media("<=tablet") {
        display: none;
        
      }
    }
    .blog_content{
      // background-color: chartreuse;
      padding: 1rem;
      margin:1rem;
      width:400px;
      height: 200px;
      text-align: center;
      .blog_header{
        padding:0.4rem;
        text-align: center;
        font-size: 1.4rem;
      }
      .blog_text{
        padding: 0.2rem;
      text-align: justify;

      }
      .blog_button{
        padding: 1rem;
        button{
          width: 150px;
          padding: 5px;
          border-radius: 7px;
          border: none;
        background-color: $yellow; 
      a{
        text-decoration: none;
        color: $black;
      }
      }
      }
    }
  }
}

//POST

.post_container{
  width: 60%;
  display: flex;
  position: relative;
  top: 10vh;
  height: 87vh;
  // bottom: 15vh;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  @include media("<=tablet") {
    width: 100%;
  }
  .post_header{
    font-size: 1.8rem;
    padding: 1rem;
    margin-top: 1rem;
  }
  .image_container{
    max-width:80%;
      max-height: 80%;
      @include media("<=tablet") {
        max-width:110%;
      max-height: 100%;
      display: flex;
      justify-content: center;
      }
    img{
      padding: 1rem;
      // width:80%;
      width:100%;
      height: 100%;
    }
  }
  .post_content{
    opacity: 0.7;
    font-weight: 400;
    // font-size: 1.9rem;
    // line-height: 2rem;
    margin: 1rem;
    text-align: justify;
    // margin-bottom: 1;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.8rem;
    font-style: oblique;

    @include media("<=tablet") {
      font-size: 1.2rem;
    }
  }
  .post_quote{
    color: $yellow;
    font-size: 1.5rem;
    padding: 1.5rem;
    margin: 1rem;
  }
}