.contact_container{
    // background-color: red;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: relative;
    top: 15vh;
    height: 82vh;
    opacity: 0.9;
    overflow-x: hidden;
    margin-bottom: 2rem;

    // .contact_header{
    //     color: red;
    //     font-size: 1.6rem;
    //     @include media("<=tablet") {
    //         font-size: 0.8rem;
    //           }
    // }

    .contact_content{
        display: flex;
        flex-direction: column;
        width: 70%;
        box-sizing: border-box;
        padding: 1rem;
        // opacity: 0.55;
        border-radius: 10px;
        // background-color: $yellow;
        background-image: linear-gradient(to bottom right, $yellow,#db36a4);
        align-items: center;
        .contact_header{
            // color: red;
            font-size: 1.4rem;
            @include media("<=tablet") {
                font-size: 1rem;
                  }
                  @include media("<=phone") {
                    font-size: 0.8rem;
                      }
        }
        input{
            border-radius: 5px;
            border: none;
        }

        @include media("<=tablet") {
        width: 100%;
          }

        .contact_name{
            width: 100%;
            @include media(">tablet") {
                display: flex;
                justify-content: space-between;

                  }
            .contact_box{
                display: flex;
                width: 100%;
                margin-top: 1rem;
                @include media(">tablet") {
                    display: flex;
                    width:48%;
                      }
                input{
                    width:100%;
                    padding: 15px;
                    @include media(">tablet") {
                        width: 100%;
                          }
                }
            }
        }
        .contact_mail{
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 1rem;
            input{
                width: 100%;
                padding: 15px;
            }
        }

        .contact_number{
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 1rem;
            input{
                width: 100%;
                padding: 15px;
            }
        }

        .contact_text{
            display: flex;
            // flex-direction: column;
            width: 100%;
            margin-top: 1rem;
            input{
                width: 100%;
                padding: 15px;
                height: 5rem;
                text-align: left;
            }
        }
        .contact_package{
            display: flex;
            flex-direction: column;
            text-align: center;

            ul{
                margin: 0;
                padding: 0;
                li{
                    display: inline;
                    list-style: none;
                }
            }
            button{
                margin: 1.2rem;
                align-self: center;
                width: 7rem;
                height: 3rem;
                border-radius: 10px;
                border: none;
                background-color: $black;
                color: $white;
                font-weight: 500;
                font-size: 1.4rem;
                opacity: 0.90;
            }
        }
    }
}