.about_me_container{
    // display: flex;
    position: relative;
    top: 15vh;
    height: 82vh;
    width: 85%;
    overflow-x: hidden;
    background-image: linear-gradient(to bottom right, $yellow,#db36a4);
    border-radius: 10px;
    @include media("<=tablet") {
    width: 100%;    
    }
    .about-header{
        width: 100%;
        display: flex;
        justify-content: center;
        h2{
            font-size: 2rem;
            font-weight: 600;
            opacity: 0.88;
        }
        @include media("<=tablet") {
            padding: 0;
            margin: 0;
            font-size: 1.5rem;
            }
    }
    .about_main{
        display: flex;
        @include media("<=tablet") {
            font-size: 1rem;
            flex-direction: column;
            text-align: center;
            opacity: 0.8;
            line-height: 1.5rem;
              }
        .aboutme_image{
            height: 50vh;
            padding: 2rem;
            @include media("<=tablet") {
                padding: 0;
                margin: 0;
                height: 70vh;
                }
            img{
                height: 100%;
            }
        }
        .aboutme_content{
            padding: 2rem;
            height: 50vh;
            opacity: 0.7;
            @include media("<=tablet") {
                padding: 0.2rem;
                padding-top: 2rem;
                // color: blue;
                  }
            .aboutme_paragraph{
                font-size: 1.5rem;
                @include media("<=LGdesktop") {
                    font-size: 1.2rem;
                      }
                      @include media("<=desktop") {
                        font-size: 1.1rem;
                          }
                          @include media("<=tablet") {
                            text-align: justify;
                            // font-size: 1rem;
                              }
            }
            a{
                color: $yellow;
            }
        }
    }
}