.packages_container{

    position: relative;
    top: 15vh;
    height: 83vh;
    width:100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-image: linear-gradient(to bottom right, $yellow,#db36a4);
    border-radius: 10px;

    .packages_about{
        padding: 2rem;
        font-weight: 500;
        font-size: 1.3rem;
        @include media("<=LGdesktop", ">desktop") {
        padding:0 1.5rem;
        font-weight: 400;
        font-size: 1.2rem;;
          }
        @include media("<=tablet") {
            margin: 1rem 0;
            padding: 0;
            text-align: justify;
              }
    }

    .packages_main{
        width: 100%;
        display: flex;
        @include media("<=tablet") {
            margin: 0;
            padding: 0;
            flex-direction: column;
            align-items: center;
              }
        .package_box{
            position: relative;
        width: 33.3333%;
        padding: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include media("<=tablet") {
            width: 100%;
            margin: 0;
            padding: 0; 
            // overflow: hidden;

              }
        h2{
            margin-bottom: 0.7rem;
            font-size: 1.4rem;
            font-weight: 700;
            text-decoration: underline;
            @include media("<=LGdesktop") {
            margin-bottom: 0rem;
            margin-top: 0rem;
            font-size: 1.2rem;
            font-weight: 700;
            }
            @include media("<=tablet") {
                font-size: 1.5rem;
                margin-top: 1rem;
                  }
        }
        p{
            text-align: justify;
            font-size: 1.2rem;
            padding-bottom: 3rem;
            @include media("<=LGdesktop") {
                font-size: 1.1rem;
                padding-bottom: 2.5rem;
                }
            @include media("<=tablet") {
                font-size: 1rem;
                padding-bottom: 6rem;

                  }
        }
        ul{
            position: absolute;
            // top: 100;
            bottom: 0;
            margin: 0;
            padding: 0;
            font-size: 1.2rem;
            @include media("<=LGdesktop") {
                font-size: 1.1rem;
                }
            @include media("<=tablet") {
                font-size: 1rem;
                  }
            li{
                list-style: none;
                text-align: center;
                padding: 0.2rem;
            }
        }
        }
    }
    .package_action{
        display: flex;
        width:100%;
        margin-top: 4rem;
        @include media("<=LGdesktop") {
            // margin-bottom: 1rem;
            margin-top: 0rem;
            }
        button{
            width: 40%;
            height: auto;
            background-color: $yellow;
            border: none;
            border-radius: 10px;
            margin-left: auto;
            margin-right: auto;
            font-size: 2rem;
            padding: 1rem;
            @include media("<=LGdesktop" , ">tablet") {
                width: 20%;
                padding: 0.5rem;
                // margin-top: 2rem;
                // text-align: center;
                }
            
        }
    }
}